import React, { useEffect, useState } from 'react';
import { createLobby, db, joinLobby } from './firebase-config';
import { useNavigate } from "react-router-dom";
import { collection, getDocs, query, where } from "firebase/firestore";
import { Box, Button, Container, TextField, Typography } from '@mui/material';

function App() {
    const [lobbyCode, setLobbyCode] = useState('');
    const [nickname, setNickname] = useState('');
    const [isCreating, setIsCreating] = useState(false);
    const [lobbyStatus, setLobbyStatus] = useState(null);
    const [nicknameStatus, setNicknameStatus] = useState(null);

    const navigate = useNavigate();

    useEffect(() => {
        if (lobbyCode) {
            checkLobbyCode(lobbyCode);
        }
    }, [lobbyCode]);

    useEffect(() => {
        if (nickname) {
            checkNickname(nickname);
        }
    }, [nickname]);


    const checkLobbyCode = async (code) => {
        try {
            const lobbiesRef = collection(db, "lobbies");
            const q = query(lobbiesRef, where("code", "==", code));
            const querySnapshot = await getDocs(q);
            if (querySnapshot.empty) {
                setLobbyStatus('available');
            } else {
                setLobbyStatus('unavailable');
            }
        } catch (error) {
            console.error("Error checking lobby code: ", error);
            setLobbyStatus('invalid');
        }
    };

    const checkNickname = async (name) => {
        if (lobbyStatus === 'unavailable') {
            try {
                const lobbiesRef = collection(db, "lobbies");
                const q = query(lobbiesRef, where("code", "==", lobbyCode));
                const querySnapshot = await getDocs(q);
                if (!querySnapshot.empty) {
                    const lobbyData = querySnapshot.docs[0].data();
                    if (lobbyData.nicknames.includes(name)) {
                        setNicknameStatus('unavailable');
                    } else {
                        setNicknameStatus('available');
                    }
                }
            } catch (error) {
                console.error("Error checking nickname: ", error);
                setNicknameStatus('invalid');
            }
        } else if (lobbyStatus === 'available') {
            setNicknameStatus(name ? 'available' : 'invalid');
        }
    };

    const handleCreateOrJoinLobby = async () => {
        try {
            let lobbyId;
            if (isCreating) {
                lobbyId = await createLobby(lobbyCode, nickname);
            } else {
                lobbyId = await joinLobby(lobbyCode, nickname);
            }
            navigate(`/lobby/${lobbyId}`, { state: { nickname, useBots: false } });
            console.log("Lobby joined/created successfully with ID:", lobbyId);
        } catch (error) {
            console.error("Failed to create or join lobby:", error);
        }
    };

    return (
        <Container maxWidth="md">
            <Box textAlign="center" mt={5}>
                <Typography variant="h3" component="h1" gutterBottom>
                    Codenames Game
                </Typography>
                <Box className="rules" mb={3} p={3} bgcolor="#f9f9f9" borderRadius={2}>
                    <Typography variant="h5" component="h2" gutterBottom>
                        Как играть в Codenames
                    </Typography>
                    <Typography variant="body1">
                        <strong>Цель игры:</strong> чтобы команда первой отгадала все свои слова-ключи, используя
                        подсказки, которые предоставляет капитан команды.
                    </Typography>
                    <Typography variant="h6" component="h3" mt={2}>
                        Правила игры:
                    </Typography>
                    <ol>
                        <li><Typography variant="body1"><strong>Разделение на команды:</strong> Игроки делятся на
                            две команды: синюю и красную. В каждой команде выбирается капитан.</Typography></li>
                        <li><Typography variant="body1"><strong>Подготовка поля:</strong> На игровом поле
                            размещается сетка из 25 слов (5х5). Некоторые слова принадлежат красной команде,
                            некоторые - синей, некоторые - нейтральные (белые), и одно слово - "убийца" (чёрное),
                            которое завершает игру в пользу противоположной команды, если оно выбрано.</Typography>
                        </li>
                        <li><Typography variant="body1"><strong>Подсказки капитанов:</strong> Капитаны видят цвет
                            каждого слова на поле. Они по очереди дают своим командам подсказки, состоящие из одной
                            связи и числа слов, которые участвуют в этой связи.</Typography></li>
                        <li><Typography variant="body1"><strong>Отгадывание:</strong> Игроки команды пытаются
                            отгадать слова на поле, которые соответствуют подсказке капитана. Они могут продолжать
                            отгадывать до тех пор, пока не совершат ошибку или не решат остановиться и передать
                            ход.</Typography>
                        </li>
                        <li><Typography variant="body1"><strong>Окончание игры:</strong> Игра заканчивается, когда
                            одна из команд отгадывает все свои слова или если одна из команд выбирает
                            "убийцу".</Typography></li>
                    </ol>
                </Box>
                <TextField
                    label="Enter Lobby Code"
                    value={lobbyCode}
                    onChange={(e) => setLobbyCode(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                />
                {lobbyStatus === 'available' && <Typography color="green">Код не используется</Typography>}
                {lobbyStatus === 'unavailable' && <Typography color="red">Лобби с таким кодом создано</Typography>}
                <TextField
                    label="Enter your nickname"
                    value={nickname}
                    onChange={(e) => setNickname(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    disabled={lobbyStatus !== 'available' && lobbyStatus !== 'unavailable'}
                />
                {nicknameStatus === 'unavailable' && <Typography color="red">Имя занято! Введите другое</Typography>}
                {nicknameStatus === 'available' && <Typography color="green">Имя свободно!</Typography>}
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => {
                        setIsCreating(true);
                        handleCreateOrJoinLobby();
                    }}
                    sx={{ m: 1 }}
                    disabled={!(lobbyStatus === 'available' && nicknameStatus === 'available')}
                >
                    Create Lobby
                </Button>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={() => {
                        setIsCreating(false);
                        handleCreateOrJoinLobby();
                    }}
                    sx={{ m: 1 }}
                    disabled={!(lobbyStatus === 'unavailable' && nicknameStatus === 'available')}
                >
                    Join Lobby
                </Button>
            </Box>
        </Container>
    );
}

export default App;
